.contain {
    min-width: 700px;
    max-width: 950px;
    width: 100%;
    margin-top: 80px;
    margin-right:  22rem;
    box-sizing: border-box;
    padding: 0 0rem;
    display: flex;
    flex-direction: column;
    height: 105%;
}

.overview {
    width: 90%;
    height: 100%;
    background: linear-gradient(to bottom, #f5f7fa 0%, #c3cfe2 50%, #a1c4b0 100%);
    border-radius: 12px;
    box-shadow: 4px 4px 20px rgba(0, 0, 0, 0.08);
    padding: 2.5rem;
    margin-right: 1rem;
    min-height: 65vh;
}

@media (max-width: 480px) {
  .contain {
      min-width: auto;        /* release width restriction */
      max-width: 100%;        /* allow shrinking */
      width: 100%;
      margin: 0 !important;   /* remove large right margin */
      padding: 0 0.5rem !important;
      margin-top: 40px;       /* optional */
      margin-right: 0 !important;
  }

  .overview {
      width: 100% !important;
      padding: 1.5rem !important;
      margin-right: 0 !important;
  }
}
