@media screen and (max-width: 480px) {
  .sidebar-container {
    width: 100px; /* Narrower on mobile */
    height: 74vh;
    right: 0;
    left: auto;
    top: 0;
    margin-top: 90px;
    padding: 10px 0;
    border-radius: 0;
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.2);
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
  }

  .sidebar-Sections {
    gap: 10px;
  }

  .section-title {
    display: none; /* Hide section titles on small screens */
  }

  .buttons-container {
    padding: 0 5px;
  }

  .sidebar-button {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 0; /* Hide text */
    padding: 8px 0;
  }

  .sidebar-button .button-icon {
    font-size: 20px;
    color: #1acb81;
  }

  .sidebar-button:hover {
    background: rgba(98, 185, 107, 0.2);
  }
}

